  font-style: normal;
}

.button_large {
  padding: 7px 47px 10px 48px;
}

.button_small {
  font-size: 17px;
  padding: 10px 24px 1px 23px;
}

.button_color {
  font-size: 17px;
  padding: 9px 21px 11px 20px;
  background-color: #1dc6a5;
  color: #fff;
  letter-spacing: 0.01em;
}

.button_light {
