  margin-top: -4px;
}

.line {
  display: flex;
  height: 6px;
  margin-top: 16px;
}

.line__part1 {
  width: 835px;
  background: #ffd04b;
  position: relative;
}

.line__part1:before {
  position: absolute;
  content: '';
  height: 6px;
  background: #ffd04b;
  width: 2000px;
  right: 100%;
}

.line__part2 {
  width: 415px;
  background: #faa61a;
  position: relative;
}

.line__part2:after {
  position: absolute;
  content: "";
  height: 6px;
  background: #faa61a;
  width: 2000px;
  left: 100%;
}

.title {
  font-family: 'FuturaPTHeavy';
  font-size: 36px;
  color: #3d3c3d;
  letter-spacing: 0.03em;
}

.mid-button {
  display: flex;
  width: 399px;
  height: 140px;
  border-radius: 10px;
}

.mid-button_1 {
  background-color: #ffd04b;
  background-image: linear-gradient(50deg, #faa51a, rgba(255, 207, 75, 0));
}

.mid-button_2 {
  background-color: #ffa33c;
  background-image: linear-gradient(50deg, #ef6e00, rgba(255, 154, 4, 0));
}

.mid-button_3 {
  background-color: #77b7e5;
  background-image: linear-gradient(50deg, #3c8fc3, rgba(119, 183, 229, 0));
}

.mid-button__icon {
